.card {
  transition: 0.3s;
  &:hover {
    box-shadow: var(--box-shadow-inset-2);
  }

  &__link {
    display: inline-block;
    width: 35%;
    height: 300px;

    @include mobile {
      align-self: center;
      width: 100%;
      margin-bottom: 10px;
    }
    @include mobile-s {
      height: 200px;
      margin-bottom: 0;
    }
  }

  &__img {
    height: 100%;
    object-fit: cover;
    border-radius: 5px;

    @include tablet {
      object-fit: contain;
    }

    @include mobile-s {
      object-fit: fill;
    }
  }

  &__body {
    width: 50%;
    height: 100%;

    @include mobile {
      display: flex;
      flex-wrap: wrap;
    }
  }

  &__title {
    margin-bottom: 20px;
  }

  &__descr {
    margin-bottom: 40px;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__stack {
    display: flex;

    .light &-img {
      box-shadow: var(--box-shadow-4);
    }

    &-img {
      width: 70px;
      height: 70px;
      padding: 15px;
      border: 1px solid transparent;
      border-radius: 50%;
      box-shadow: var(--box-shadow-icons);

      transition: 0.2s;

      &:not(:last-child) {
        margin-right: 20px;
      }

      &:hover {
        border: 1px solid var(--color-accent);
      }

      @include tablet {
        width: 60px;
        height: 60px;
      }

      @include mobile {
        width: 40px;
        height: 40px;
        padding: 10px;

        &:not(:last-child) {
          margin-right: 10px;
        }
      }
    }

    @include tablet {
    }
  }
}
