.footer {
  padding-block: 60px;

  // &__container {
  // }

  &__title {
    margin-bottom: 40px;
    font-size: 40px;
  }

  &__body {
    display: flex;
    justify-content: space-between;
    padding: 50px;
    width: 100%;
    height: 400px;
    border-radius: var(--border-radius-small);
    box-shadow: var(--box-shadow-small);

    &:hover {
      box-shadow: var(--box-shadow-inset-2);
    }

    @include tablet {
      padding: 30px;
      height: auto;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__subTitle {
    font-size: 50px;
    color: var(--color-accent);

    @include tablet {
      font-size: 40px;
    }

    @include mobile {
      font-size: 25px;
      margin-bottom: 20px;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__socials .socials {
    &__list {
      display: flex;

      @include mobile {
        margin-bottom: 20px;
      }
    }

    &__item {
      position: relative;
      width: 70px;
      height: 70px;
      padding: 15px;
      border: 1px solid transparent;
      border-radius: 50%;
      box-shadow: var(--box-shadow-icons);

      transition: 0.2s;

      &:not(:last-child) {
        margin-right: 20px;
      }

      &:hover {
        border: 1px solid var(--color-accent);
      }

      @include tablet {
        width: 60px;
        height: 60px;
      }

      @include mobile {
        width: 40px;
        height: 40px;
        padding: 10px;
      }
    }

    &__link {
      @include abs-center;
      display: inline-block;
      width: 70px;
      height: 70px;

      & > svg {
        @include abs-center;
        width: 40px;
        height: 40px;

        @include tablet {
          width: 30px;
          height: 30px;
        }

        @include mobile {
          width: 20px;
          height: 20px;
        }
      }

      &:hover {
        color: var(--color-accent);
      }
    }
  }

  .light &__body {
    box-shadow: var(--box-shadow-4);

    &:hover {
      box-shadow: var(--box-shadow-inset-2);
    }
  }
}

.light .socials__item {
  box-shadow: var(--box-shadow-4);
}
