@keyframes blob {
  from {
    transform: translate(-30px, 0px) rotate(-95deg);
    border-radius: 24% 76% 35% 65% / 27% 36% 64% 73%;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  }

  to {
    transform: translate(30px, 20px) rotate(-10deg);
    border-radius: 76% 24% 33% 67% / 68% 55% 45% 32%;
    box-shadow: -10px 10px 15px rgba(0, 0, 0, 0.5);
  }
}

.hero__scene {
  display: flex;
  &-icon {
    height: 400px;
    width: 400px;

    z-index: -1;
    margin-right: 100px;
    background: #e96543;
    background: linear-gradient(135deg, #e96543, #904e95);
    animation: blob infinite alternate 8s;
    transition: all 0.3s ease-in-out;

    &:hover {
      filter: blur(10px);
      box-shadow: inset 0 0 0 5px rgba(230, 47, 47, 0.774),
        inset 100px 100px 0 0px #d1185f, inset 200px 200px 0 0px #784ba8,
        inset 300px 300px 0 0px #874794;
    }

    @include tablet {
      width: 200px;
      height: 200px;
      margin-right: 50px;
    }

    @include mobile {
      display: none;
    }
  }
}
