:root {
  //colors

  --color-light: #f5f5f5;
  --color-dark: #1a1a1d;
  --color-accent: #904e95;
  --color-alt-dark: #1d1b1b;
  --c-dark-for-shadow: #000;

  //light-mode

  --color-theme: #1a2a1d;

  //other
  --base-transition-duration: 0.2s;
  --transition-duration: 0.3s;

  //border-radius

  --border-radius-small: 20px;
  --border-radius-medium: 25px;
  --border-radius-large: 30px;

  //shadows

  --box-shadow: -12px -12px 12px hsla(0, 0%, 100%, 0.32),
    12px 12px 12px rgba(0, 0, 0, 0.32);
  --box-shadow-2: -4px -4px 12px hsla(0, 0%, 100%, 0.8),
    4px 4px 12px var(--c-dark-for-shadow);
  --box-shadow-3: -4px -4px 12px hsla(0, 0%, 100%, 0.05),
    4px 4px 12px var(--c-dark-for-shadow);
  --box-shadow-3-transparent: -4px -4px 12px hsla(0, 0%, 100%, 0),
    4px 4px 12px transparent;
  --box-shadow-4: -8px -8px 12px hsla(0, 0%, 100%, 0.8),
    8px 8px 12px rgba(0, 0, 0, 0.05);
  --box-shadow-4-transparent: -8px -8px 12px hsla(0, 0%, 100%, 0),
    8px 8px 12px transparent;
  --box-shadow-5: -4px -4px 12px hsla(0, 0%, 100%, 0.05), 4px 4px 12px;
  --box-shadow-6: -4px -4px 12px hsla(0, 0%, 100%, 0.05),
    4px 4px 20px rgba(0, 0, 0, 0.25);
  --box-shadow-inset: inset -22px -14px 14px 2px hsla(0, 0%, 100%, 0.015),
    inset 8px 4px 20px 12px rgba(0, 0, 0, 0.8);
  --box-shadow-inset-transparent: inset -22px -14px 14px 2px
      hsla(0, 0%, 100%, 0),
    inset 8px 4px 20px 12px transparent;
  --box-shadow-inset-2: inset -2px -2px 4px hsla(0, 0%, 100%, 0.1),
    inset 2px 2px 4px rgba(0, 0, 0, 0.5);
  --box-shadow-inset-2-transparent: inset -2px -2px 4px hsla(0, 0%, 100%, 0),
    inset 2px 2px 4px transparent;

  --box-shadow-small: -1px -1px 4px hsla(0, 0%, 100%, 0.1),
    4px 4px 5px rgba(0, 0, 0, 0.5);
  // --box-shadow-inset: 0px 0px 25px 9px rgba(0, 0, 0, 0.64) inset;
  --box-shadow-icons: inset -2px -2px 4px hsla(0, 0%, 100%, 0.1),
    inset 2px 2px 4px rgba(0, 0, 0, 0.5);
}
