.exp {
  &__card {
    display: flex;
    flex-direction: column;
    padding: 30px;
    width: 100%;
    height: auto;
    border-radius: var(--border-radius-small);
    box-shadow: var(--box-shadow-small);

    &:hover {
      box-shadow: var(--box-shadow-inset-2);
    }

    @include tablet {
      padding: 20px;
    }
  }

  &__title {
    order: 2;
    margin-bottom: 20px;
    font-size: 35px;

    @include tablet {
      font-size: 30px;
    }
  }

  &__date {
    order: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25%;
    min-height: 60px;
    margin-bottom: 25px;
    text-transform: uppercase;
    font-weight: normal;
    color: var(--color-accent);
    border: 1px solid transparent;
    border-radius: var(--border-radius-small);
    box-shadow: var(--box-shadow-inset-2);

    transition: all 0.2s;

    &:hover {
      border: 1px solid var(--color-accent);
    }

    @include tablet {
      width: 30%;
      margin-bottom: 15px;
    }
    @include mobile {
      width: 50%;
    }
    @include mobile-s {
      width: 100%;
    }
  }

  &__subTitle {
    order: 3;
    margin-bottom: 10px;
    font-size: 25px;

    @include tablet {
      font-size: 20px;
    }
  }

  &__body {
    order: 4;
    margin-bottom: 30px;
  }

  &__tech {
    order: 5;
    display: flex;
  }

  &__icon {
    width: 70px;
    height: 70px;
    padding: 15px;
    border: 1px solid transparent;
    border-radius: 50%;
    box-shadow: var(--box-shadow-icons);

    transition: 0.2s;

    &:not(:last-child) {
      margin-right: 20px;
    }

    &:hover {
      border: 1px solid var(--color-accent);
    }

    @include tablet {
      width: 60px;
      height: 60px;
    }
    @include mobile {
      width: 40px;
      height: 40px;
      padding: 10px;
    }
  }

  .light &__card {
    box-shadow: var(--box-shadow-4);

    &:hover {
      box-shadow: var(--box-shadow-inset-2);
    }
  }

  .light &__date {
    box-shadow: var(--box-shadow-inset-2);
  }

  .light &__icon {
    box-shadow: var(--box-shadow-4);
  }
}
