.header {
  top: -70px;
  left: 0;
  width: 100%;
  height: 70px;

  transition: all 0.2s;

  &__container {
    height: 100%;
    max-width: 1500px;
    padding-inline: 30px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    max-width: 300px;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;

    & > h1 {
      margin: 0;
    }

    @include tablet {
      font-size: 16px;
    }
  }

  &__nav {
    margin-right: 5%;

    @include tablet {
      margin-right: 0;
    }
  }

  &__nav-list {
    display: flex;
  }

  &__nav-item {
    font-size: 20px;
    font-weight: 300;

    &:not(:last-child) {
      margin-right: 25px;
    }

    &:hover {
      color: var(--color-accent);
    }

    @include mobile {
      display: none;
    }
  }
}

.header.is-scrolled {
  position: fixed;
  z-index: 10;
  height: 60px;
  transform: translateY(70px);
  background-color: #1a1a1d;
  box-shadow: var(--box-shadow);
}

.light .header.is-scrolled {
  background-color: #f5f5f5;
}
