.portfolio {
  padding-block: 60px;

  &__title {
    margin-bottom: 40px;
    font-size: 40px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__card {
    display: flex;
    justify-content: space-between;
    height: auto;
    margin-bottom: 50px;
    padding: 35px 35px;
    border-radius: var(--border-radius-small);
    box-shadow: var(--box-shadow-small);

    @include tablet {
      padding: 20px;
    }

    @include mobile {
      flex-direction: column;
      padding: 15px;
    }

    @include mobile-s {
      height: auto;
    }
  }

  .light &__card {
    box-shadow: var(--box-shadow-4);
  }
}
