.header__btn-theme {
  position: relative;
  width: 35px;
  height: 35px;
  border: none;

  &-icon {
    @include abs-center;
    width: 35px;
    height: 35px;
    stroke-linecap: round;
    opacity: 0.8;
    transition: all 0.1s ease;

    &:hover {
      opacity: 1;
      transform: scale(1.2);
    }
  }
}
