.skills {
  padding-block: 60px;
  transition: all 0.3s;

  &__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 120px;

    @include tablet {
      column-gap: 60px;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
    }
  }

  &__hard {
    &-title {
      font-size: 40px;
      font-weight: 400;
      margin-bottom: 40px;
    }

    @include mobile {
      margin-bottom: 60px;
    }
  }

  &__soft {
    &-title {
      font-size: 40px;
      font-weight: 400;
      margin-bottom: 40px;
    }
  }

  &__list-item {
    display: flex;
    align-items: center;
    justify-content: start;
    min-height: 70px;
    padding: 20px;
    font-weight: lighter;
    border-radius: var(--border-radius-small);
    box-shadow: var(--box-shadow-small);
    user-select: none;
    transition: all 0.2s;

    &:not(:last-child) {
      margin-bottom: 20px;
    }

    &:hover {
      box-shadow: var(--box-shadow-inset-2);
      color: var(--color-accent);
    }
  }

  .light &__list-item {
    box-shadow: var(--box-shadow-4);

    &:hover {
      box-shadow: var(--box-shadow-inset-2);
    }
  }
}
