.hero {
  padding-block: 60px;

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__body {
    width: 55%;

    @include mobile {
      width: 100%;
    }
  }

  &__descr {
    margin-bottom: 40px;
    font-size: 45px;
    font-weight: 400;
    text-wrap: nowrap;
    user-select: none;

    &::first-line {
      color: var(--color-accent);
    }

    @include tablet {
      font-size: 30px;
    }

    @include mobile {
      font-size: 30px;
    }
    @include mobile-s {
      font-size: 20px;
    }
  }

  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    width: 190px;
    padding: 10px;
    font-size: 22px;
    border-radius: var(--border-radius-small);
    box-shadow: var(--box-shadow-small);

    &:hover {
      border: 1px solid var(--color-accent);
    }

    @include tablet {
      width: 140px;
      font-size: 20px;
    }
    @include mobile {
      width: 120px;
      font-size: 16px;
    }
  }

  .light &__btn {
    box-shadow: var(--box-shadow-6);
  }

  @keyframes animate {
    0%,
    100% {
      opacity: 1;
    }

    50% {
      opacity: 0;
    }
  }

  &--anim {
    position: relative;
    animation: animate 5s infinite;
  }

  &__scene {
    max-width: 50%;
  }
}
